*, :before, :after {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul, ol, li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit;
}

html, body {
  height: 100%;
  line-height: 1;
}

textarea, button, input {
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
  font-family: inherit;
}

button {
  cursor: pointer;
  background-color: inherit;
}

body {
  color: #fff;
  background-color: #000;
  font-family: Arial, Helvetica, sans-serif;
}

.wrapper {
  min-height: 100%;
  overflow: clip;
}

.page {
  width: 100%;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  padding-top: 10vh;
  padding-bottom: 10vh;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.slider {
  flex: auto;
  overflow: hidden;
}

.slider__wrapper {
  display: flex;
}

.slider__slide {
  flex-shrink: 0;
}

.slide__body {
  width: 60vh;
  max-width: 90vw;
  margin: 0 auto;
  box-shadow: 0 0 1.875rem rgba(0, 0, 0, .5);
}

.slide__image {
  cursor: pointer;
  padding-bottom: 120%;
  position: relative;
}

.slide__picture {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.slider__scrollbar {
  width: 100%;
  height: .3125rem;
  opacity: .5;
  position: absolute;
  bottom: 0;
  left: 0;
}

.slider__scrollbar:focus, .slider__scrollbar:hover, .slider__scrollbar:active {
  height: .9375rem;
  opacity: .8;
}

.slider__scrollbar .swiper-scrollbar-drag {
  height: 100%;
  opacity: 1;
  cursor: pointer;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
}

